@import '../colors.scss';

body {
    font-family: $font-text;
}

.admin{
    h1 {
        margin: 2rem;
    }
    a {
    text-decoration: none;  
    }

    .admin-container{
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 1fr;
        justify-items: start;
        margin: 2rem;
    }
        .left-side, .right-side{
            h2 {
                text-align: left;
                padding: 0;
            }
            button {
                margin: 0 auto;
                display: block;
                background-color: $button-color;
                width: 12rem;
                height: 2rem;

                a {
                    color: white;
                    font-family: $font-text;
                }
            }
            .events-btn{
                display: flex;
                margin: 0 auto;
                width: 50%;
                padding: 0;
            }
            .table-style {
                border-collapse: collapse;
                margin: 25px 0px;
                font-size: $table_font_size;
                min-width: 300px;
                box-shadow: $main-shadow;

                thead tr {
                    background-color: $thead-color;
                    color: #ffffff;
                    text-align: left;
                }

                th, td {
                    padding: 10px 15px;
                }

                tbody tr {
                    border-bottom: 1px solid #dddddd;
                }

                tbody tr:nth-of-type(even){
                    background-color: $background;
                }

                tr:hover td{
                    color:black;
                    border-top: 1px solid grey;
                    border-bottom: 1px solid grey;
                }
    
                tbody tr:last-of-type {
                    border-bottom: 2px solid $thead-color;
            }
        }
    }
}